:root {
  --color-primary: #4a148c; /* purple darken-4 */
  --color-secondary: #009688; /* Teal */
  --color-dark: #263238; /* blue-grey darken-4 */
}

a {
  text-decoration: none;
  color: var(--color-secondary);
}

a:visited { color: var(--color-primary); }

@keyframes sk-rotate {
  100% { transform: rotate(360deg); }
}

@keyframes sk-bounce {
  0%, 100% { 
    transform: scale(0.0);
  } 50% { 
    transform: scale(1.0);
  }
}