body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 95%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 95%);
    background: -webkit-linear-gradient(left, rgba(74, 20, 140, 0.5), rgba(0, 150, 136, 0.5)), url(/static/media/hero.02959b03.jpg);
    background: linear-gradient(to right, rgba(74, 20, 140, 0.5), rgba(0, 150, 136, 0.5)), url(/static/media/hero.02959b03.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    padding: 3rem 0;
    min-height: 100vh;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Header-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Header-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.Header-imgContainer {
    --size: 10rem;
    width: var(--size);
    height: var(--size);
    background-color: #fff;
    border-radius: 50%;
    border: .3rem solid rgba(255,255,255, .8);
    margin: 1rem auto;
    overflow: hidden;
}

@-webkit-keyframes ColorChange {
    from { color: #fff; }
    to { color: rgb(74, 20, 140); }
}

@keyframes ColorChange {
    from { color: #fff; }
    to { color: rgb(74, 20, 140); }
}
.MainFooter,
.MainFooter-skills {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
}

.MainFooter { padding: 1rem; }

.Repositories {
    -webkit-clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 95%);
            clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 95%);
    background: -webkit-linear-gradient(left, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.6)), url(/static/media/hero.02959b03.jpg);
    background: linear-gradient(to right, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.6)), url(/static/media/hero.02959b03.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    width: 100%;
    padding: 10% 0 12%;
    margin: 4rem 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

@media screen and (min-width: 640px) {
    .Repositories { padding: 2% 0 6%; }
}
.Aboutme {
    padding: 1.5rem;
}
.Spinner {
  margin: 100px auto;
  width: 40px;
  height: 40px;
  position: relative;
  text-align: center;

  -webkit-animation: sk-rotate 2.0s infinite linear;

          animation: sk-rotate 2.0s infinite linear;
}

.Spinner-dot1, .Spinner-dot2 {
  width: 60%;
  height: 60%;
  display: inline-block;
  position: absolute;
  top: 0;
  background-color: rgb(0, 150, 136);
  border-radius: 100%;

  -webkit-animation: sk-bounce 2.0s infinite ease-in-out;

          animation: sk-bounce 2.0s infinite ease-in-out;
}

.Spinner-dot2 {
  top: auto;
  bottom: 0;
  -webkit-animation-delay: -1.0s;
          animation-delay: -1.0s;
}

@-webkit-keyframes sk-rotate {
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes sk-rotate {
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes sk-bounce {
  0%, 100% { 
    -webkit-transform: scale(0.0); 
            transform: scale(0.0);
  } 50% { 
    -webkit-transform: scale(1.0); 
            transform: scale(1.0);
  }
}

@keyframes sk-bounce {
  0%, 100% { 
    -webkit-transform: scale(0.0); 
            transform: scale(0.0);
  } 50% { 
    -webkit-transform: scale(1.0); 
            transform: scale(1.0);
  }
}

:root {
  --color-primary: #4a148c; /* purple darken-4 */
  --color-secondary: #009688; /* Teal */
  --color-dark: #263238; /* blue-grey darken-4 */
}

a {
  text-decoration: none;
  color: #009688;
  color: var(--color-secondary);
}

a:visited { color: #4a148c; color: var(--color-primary); }

@-webkit-keyframes sk-rotate {
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes sk-rotate {
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes sk-bounce {
  0%, 100% { 
    -webkit-transform: scale(0.0); 
            transform: scale(0.0);
  } 50% { 
    -webkit-transform: scale(1.0); 
            transform: scale(1.0);
  }
}

@keyframes sk-bounce {
  0%, 100% { 
    -webkit-transform: scale(0.0); 
            transform: scale(0.0);
  } 50% { 
    -webkit-transform: scale(1.0); 
            transform: scale(1.0);
  }
}
