.Header {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 95%);
    background: linear-gradient(to right, rgba(74, 20, 140, 0.5), rgba(0, 150, 136, 0.5)), url('../../static/hero.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    padding: 3rem 0;
    min-height: 100vh;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Header-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Header-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.Header-imgContainer {
    --size: 10rem;
    width: var(--size);
    height: var(--size);
    background-color: #fff;
    border-radius: 50%;
    border: .3rem solid rgba(255,255,255, .8);
    margin: 1rem auto;
    overflow: hidden;
}

@keyframes ColorChange {
    from { color: #fff; }
    to { color: rgb(74, 20, 140); }
}